import React from "react";
import { Button, Paper } from "@mui/material";
import { useTranslation } from "react-i18next";
import styles from "./Help.module.css";

type TSections = "how-to" | "about";

export const Help: React.FC = () => {
    const { t } = useTranslation();

    const [ sectionOpen, setSectionOpen ] = React.useState<TSections | null>(null);

    const switchTab = (tab: TSections) => {
        setSectionOpen(sectionOpen === tab ? null : tab);
    };

    let openComponent = null;
    if (sectionOpen === "how-to") {
        openComponent = "       , , -,    .      .";
    }
    if (sectionOpen === "about") {
        openComponent = (
            <>
                <div>  &nbsp;  1898 , &nbsp;<a target="_blank" href="https://twitter.com/kederlone/status/1085966047847596033"></a> &nbsp;  @kederlone.  <a target="_blank" href="/poetry-riddle-react/img/Niva_scan.jpg"> </a>    (    <a target="_blank" href="https://runivers.ru/lib/book9158/481192/">  </a>,  42,  PDF-  399.</div>
                <div> &nbsp; &nbsp; ,  &nbsp; .   (),  &nbsp;    .     .</div>
            </>
        );
    }

    const textNode = openComponent === null ? null : <Paper elevation={3} className={styles.text}>{openComponent}</Paper>;

    return (
        <div className={styles.wrapper}>
            <div className={styles.buttons}>
                <Button variant={sectionOpen === "how-to" ? "outlined" : "text"} onClick={() => { switchTab("how-to") }}>{t(" ")}</Button>
                <span className={styles.divider}></span>
                <Button variant={sectionOpen === "about" ? "outlined" : "text"} onClick={() => { switchTab("about") }}>{t(" ")}</Button>
            </div>
            {textNode}
        </div>
    );
};
